/** Media Queries **/
html {
  font-size: 62.5%;
  box-sizing: border-box;
  height: 100%;
}

body {
  min-height: 100%;
  display: flex;
  flex-direction: column;
  font-family: "Poppins", sans-serif;
  font-weight: 300;
  font-size: 1.6rem;
  color: #26282E;
}

*,
*:before,
*:after {
  box-sizing: inherit;
}

.container {
  width: 95%;
  max-width: 120rem;
  margin: 0 auto;
}

.container-sm {
  width: 95%;
  max-width: 40rem;
  margin: 0 auto;
}

.container-md {
  width: 95%;
  max-width: 75rem;
  margin: 0 auto;
}

a {
  text-decoration: none;
}

img {
  max-width: 100%;
}

h1,
h2,
h3 {
  font-family: "Roboto", sans-serif;
  margin: 0;
}

h1 {
  font-size: 3.5rem;
}

h2 {
  font-size: 3rem;
}

h3 {
  font-size: 2.5rem;
  text-align: center;
}

p {
  font-size: 1.6rem;
  margin: 0;
}

.tittle-app {
  font-weight: 900;
  font-size: 4rem;
}
@media (max-width: 768px) {
  .tittle-app {
    text-align: center;
  }
}

.tagline {
  font-size: 1.8rem;
  font-family: "Roboto", sans-serif;
}
@media (max-width: 768px) {
  .tagline {
    text-align: center;
  }
}

.page-description {
  font-weight: 500;
  font-size: 2.5rem;
}

.form {
  margin-top: 3rem;
}
.form .field {
  display: flex;
  flex-direction: column;
  justify-content: center;
  margin-bottom: 3rem;
}
.form .field input:not([type=submit]) {
  background-color: transparent;
  font-weight: 300;
  color: #545966;
  padding: 1rem 0 0.5rem 0;
  border: none;
  outline: none;
  border-bottom: 0.1rem solid #dad8d8;
  transition: 0.3s;
}
.form .field input:not([type=submit]):hover {
  border-bottom: 0.1rem solid #9c9797;
}

.actions {
  margin-top: 5rem;
}
.actions__link {
  margin-bottom: 2rem;
}
.actions__link:last-child {
  margin-bottom: 0;
}
.actions a {
  color: #595E67;
  transition: 0.3s;
}

.alert {
  margin-top: 1.5rem;
  padding: 1rem 1rem;
  width: 100%;
  background-color: transparent;
  font-size: 1.4rem;
  font-weight: 300;
  transition: 0.2s;
  text-align: left;
}
.alert.error {
  color: #d42727;
  border-left: 0.2rem solid #df4f4f;
}
.alert.error:hover {
  background-color: #fdf3f3;
  border-left: 0.5rem solid #df4f4f;
}
.alert.succes {
  color: #547d41;
  border-left: 0.2rem solid #6a9f52;
}
.alert.succes:hover {
  background-color: #dbe9d4;
  border-left: 0.5rem solid #6a9f52;
}
.alert:hover {
  cursor: pointer;
  transform: translateX(1.2rem);
}

.sweetalert.text {
  font-size: 1.8rem;
}
.sweetalert.actions {
  width: 28rem;
  display: flex;
  justify-content: space-around;
}
.sweetalert.btn-success, .sweetalert.btn-danger {
  padding: 1rem;
  font-size: 1.6rem;
  border: none;
  border-radius: 0.5rem;
  color: #FFFFFF;
  transition: 0.3s;
}
.sweetalert.btn-success {
  background-color: #47A8BD;
}
.sweetalert.btn-success:hover {
  background-color: #37889a;
}
.sweetalert.btn-danger {
  background-color: #df4f4f;
}
.sweetalert.btn-danger:hover {
  background-color: #d42727;
}

.form-dashboard {
  margin-top: 3rem;
  padding: 2rem;
  border-radius: 0.5rem;
  background-color: #FFFFFF;
}
.form-dashboard .field {
  display: flex;
  margin-bottom: 3rem;
}
@media (min-width: 1024px) {
  .form-dashboard .field {
    align-items: center;
  }
}
@media (max-width: 1024px) {
  .form-dashboard .field {
    flex-direction: column;
    justify-content: center;
  }
  .form-dashboard .field label {
    margin-bottom: 1rem;
  }
}
.form-dashboard .field label {
  max-width: 20rem;
  width: 100%;
}
.form-dashboard .field input:not([type=submit]) {
  flex: 1;
  outline: none;
  background-color: transparent;
  padding: 1rem;
  font-weight: 300;
  border-radius: 0.5rem;
  border: 0.1rem solid #dad8d8;
  color: #313440;
  transition: 0.3s;
}
.form-dashboard .field input:not([type=submit]):hover {
  border: 0.1rem solid #313440;
}
@media (min-width: 1024px) {
  .form-dashboard .button-create {
    border: none;
    border-radius: 0.5rem;
    display: "inline-block";
    padding: 1.5rem 2rem;
    font-size: 1.8rem;
    font-weight: 500;
    text-align: center;
    background-color: #1e7df0;
    color: #FFFFFF;
    transition: 0.3s;
    padding: 1rem 2rem;
    font-size: 1.6rem;
  }
  .form-dashboard .button-create:hover {
    background-color: #0e64cd;
    cursor: pointer;
  }
}
@media (max-width: 1024px) {
  .form-dashboard .button-create {
    border: none;
    border-radius: 0.5rem;
    display: "inline-block";
    padding: 1.5rem 2rem;
    font-size: 1.8rem;
    font-weight: 500;
    text-align: center;
    background-color: #1e7df0;
    color: #FFFFFF;
    transition: 0.3s;
    width: 100%;
  }
  .form-dashboard .button-create:hover {
    background-color: #0e64cd;
    cursor: pointer;
  }
}

.auth__view {
  min-height: 100vh;
  background-image: url("../img/background.jpg");
  background-repeat: no-repeat;
  background-position: center center;
  background-attachment: fixed;
  background-size: cover;
}

.header-auth {
  padding: 1rem 5rem;
  background-color: #FFFFFF;
}
@media (max-width: 768px) {
  .header-auth {
    padding: 2rem 0;
  }
}
@media (min-width: 768px) {
  .header-auth .tittle-app {
    max-width: 20rem;
  }
}
@media (max-width: 768px) {
  .header-auth .tittle-app {
    max-width: 30rem;
    font-size: 5rem;
