.signup-view .auth-box .button-input {
  border: none;
  border-radius: 0.5rem;
  display: "block";
  padding: 1.5rem 2rem;
  font-size: 1.8rem;
  font-weight: 500;
  text-align: center;
  background-color: #d48b4f;
  color: #FFFFFF;
  transition: 0.3s;
