}

.reset-view .tittle-app {
  color: transparent;
  background: linear-gradient(to right, #df4f4f 0%, #bd4559 100%);
  background-clip: text;
}
.reset-view .auth-box .button-input {
  border: none;
  border-radius: 0.5rem;
  display: "block";
  padding: 1.5rem 2rem;
  font-size: 1.8rem;
  font-weight: 500;
  text-align: center;
  background-color: #bd4559;
  color: #FFFFFF;
