.dashboard {
  background-color: #EFF2F6;
  background-size: cover;
  background-attachment: fixed;
  min-height: 100vh;
}
@media (min-width: 768px) {
  .dashboard {
    display: flex;
    height: 100vh;
  }
}
.dashboard .main {
  flex: 1;
}
@media (min-width: 768px) {
  .dashboard .main {
    overflow: scroll;
  }
}
.dashboard .content {
  padding: 2rem 1rem;
  margin: 1rem;
  border-radius: 0.5rem;
}
.dashboard .content .page-name {
  font-size: 2.4rem;
  text-align: center;
  margin-bottom: 2rem;
}

.sidebar {
  background-color: #EFF2F6;
  padding: 1rem 2rem;
}
@media (min-width: 768px) {
  .sidebar {
    border-right: 1px solid #d5dbe5;
  }
}
.sidebar h2 {
  margin-top: 1rem;
  text-align: center;
  color: #1e7df0;
}
.sidebar .sidebar-nav {
  margin-top: 4rem;
}
.sidebar .sidebar-nav a {
  margin-bottom: 1.5rem;
  padding: 1rem;
  border-radius: 0.5rem;
  display: block;
  font-weight: 500;
  font-size: 1.4rem;
  color: #26282E;
  transition: 0.3s;
}
@media (max-width: 768px) {
  .sidebar .sidebar-nav a {
    font-size: 1.6rem;
  }
}
.sidebar .sidebar-nav a:hover {
  background-color: #d5dbe5;
}
.sidebar .sidebar-nav a.active {
  background-color: #d5dbe5;
}

.bar {
  display: flex;
  margin: 1rem;
  padding: 1rem;
  padding: 1rem;
  align-items: center;
  justify-content: space-between;
  border-bottom: 1px solid #d5dbe5;
}
.bar p span {
  color: #1e7df0;
  font-weight: 500;
}
.bar .logout {
  border-radius: 0.5rem;
  padding: 0.5rem;
  font-weight: 500;
  color: #d63e5f;
  transition: 0.2s;
}
.bar .logout:hover {
  background-color: #f7d0d0;
}

.dashboard-projects {
  margin-top: 5rem;
}
.dashboard-projects .no-projects a {
  color: #1e7df0;
  font-weight: 500;
}
.dashboard-projects .projects-list {
  list-style: none;
  padding: 0;
}
@media (max-width: 768px) {
  .dashboard-projects .projects-list {
    display: grid;
    gap: 1rem;
    grid-template-columns: repeat(1, 1fr);
  }
}
@media (min-width: 768px) {
  .dashboard-projects .projects-list {
    display: grid;
    gap: 1rem;
    grid-template-columns: repeat(2, 1fr);
  }
}
@media (min-width: 1024px) {
  .dashboard-projects .projects-list {
    display: grid;
    gap: 1rem;
    grid-template-columns: repeat(3, 1fr);
  }
}

.project {
  border: solid 1px #B0BCCE;
  display: flex;
  align-items: center;
  padding: 5rem 1rem;
  border-radius: 0.5rem;
  background-color: #FFFFFF;
  transition: 0.2s;
  line-height: 2rem;
}
.project:hover {
  transform: rotate(1deg);
  box-shadow: -10px 10px 12px -1px rgba(67, 68, 69, 0.2);
}
.project__content {
  position: relative;
  z-index: 10;
  display: flex;
  align-items: center;
  justify-content: space-between;
  gap: 1rem;
  padding: 0.5rem 1rem;
  border-radius: 0.5rem;
  color: #26282E;
  transition-property: color;
  transition-duration: 0.3s;
}
.project__content i {
  z-index: 10;
  color: #1e7df0;
}
.project__content:hover {
  color: #FFFFFF;
}
.project__content:hover i {
  color: #FFFFFF;
}
.project__content:hover:before {
  border-radius: 0.5rem 0 0 0.5rem;
}
.project__content:after {
  content: "";
  height: 100%;
  left: 0;
  top: 0;
  width: 0px;
  position: absolute;
  transition: all 0.5s ease 0s;
  -webkit-transition: all 0.5s ease 0s;
  z-index: -1;
}
.project__content:hover:after {
  width: 100%;
  border-radius: 0.5rem;
}
.project__content:after {
  background: #1e7df0;
}

