.tasks-list {
  list-style: none;
  padding: 0;
  margin-top: 5rem;
  display: flex;
  gap: 1rem;
  flex-direction: column;
}

.task {
  padding: 2rem 0;
  display: flex;
  flex-direction: column;
  gap: 2rem;
  border-bottom: solid 1px #B0BCCE;
}
.task:last-of-type {
