.task-state, .task-delete {
  border: none;
  border-radius: 1rem;
  padding: 0.5rem 1rem;
  color: #26282E;
  transition: 0.2s;
}
.task-state:hover, .task-delete:hover {
  transform: scale(0.95);
}
.task-state.complete {
  background-color: #7ccbf3;
}
.task-state.complete:hover {
  background-color: #4eb9ef;
}
.task-state.pending {
  background-color: #f0c88c;
}
.task-state.pending:hover {
  background-color: #eab25e;
}
.task-delete {
  background-color: #e77a7a;
}
.task-delete:hover {
  background-color: #df4f4f;
}

.modal {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: rgba(35, 37, 42, 0.544);
}
.modal .form-task {
  padding: 2rem;
  border-radius: 0.5rem;
  background-color: #FFFFFF;
  transform: translateY(-30rem);
  transition-property: all;
  transition-duration: 0.3s;
  transition-timing-function: ease-in;
  opacity: 0;
}
.modal .form-task.animation {
  position: relative;
  top: 50%;
  transform: translateY(-50%);
  opacity: 1;
}
.modal .form-task.close {
  transform: translateY(50%);
  opacity: 0;
}
.modal .form-task legend {
  display: block;
  margin-bottom: 2rem;
  font-weight: 500;
  font-size: 2rem;
  text-align: center;
}
.modal .form-task .field {
  display: flex;
  margin-top: 3rem;
  margin-bottom: 3rem;
}
.modal .form-task .field:last-of-type {
  margin-bottom: 0;
}
@media (max-width: 1024px) {
  .modal .form-task .field {
    flex-direction: column;
    justify-content: center;
    gap: 2rem;
  }
}
@media (min-width: 1024px) {
  .modal .form-task .field {
    align-items: center;
  }
}
.modal .form-task .field label {
  width: 100%;
}
@media (min-width: 1024px) {
  .modal .form-task .field label {
    max-width: 20rem;
  }
}
.modal .form-task .field input:not([type=submit]) {
  flex: 1;
  outline: none;
  border-radius: 0.5rem;
  background-color: transparent;
  padding: 1rem;
  font-weight: 300;
  border: 0.1rem solid #dad8d8;
  color: #050506;
  transition: 0.3s;
}
.modal .form-task .field input:not([type=submit]):hover {
  border: 0.1rem solid #313440;
}
.modal .form-task__buttons {
  justify-content: space-between;
}
.modal .form-task__buttons .button-new-task {
  border: none;
  border-radius: 0.5rem;
  display: "inline-block";
  padding: 1.5rem 2rem;
  font-size: 1.8rem;
  font-weight: 500;
  text-align: center;
  background-color: #1e7df0;
  color: #FFFFFF;
  transition: 0.3s;
}
.modal .form-task__buttons .button-new-task:hover {
  background-color: #0e64cd;
  cursor: pointer;
}
.modal .form-task__buttons .close-modal {
  border: none;
  border-radius: 0.5rem;
  display: "inline-flex";
  padding: 1.5rem 2rem;
  font-size: 1.8rem;
  font-weight: 500;
  text-align: center;
  background-color: #595E67;
  color: #FFFFFF;
  transition: 0.3s;
}
.modal .form-task__buttons .close-modal:hover {
  background-color: #41454c;
  cursor: pointer;
}

.error404 {
  position: absolute;
  display: flex;
  padding: 3rem;
  justify-content: center;
  align-items: center;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
}
@media (max-width: 768px) {
  .error404 {
    flex-direction: column;
  }
}
.error404__title {
  text-align: center;
  color: #1E3888;
}